import React from 'react';
import CountUp from 'react-countup';
import { CounterData } from '../../../database/pages/aboutus/database';
import { Container, Row, Col } from 'reactstrap'

const otfData = [
    {
        img: "",
        title: "",
        subtitle: ""
    }
]
const Counter = () => (
    <section className="resume counter bg-pink">
        <Slider className="default-dots gym-slider" id="gym-slider" {...settings}>
            {
                otfData.map((x, i) => (
                    <div className="item" key={i}>
                        <div className="gym-header bg"
                            style={{ backgroundImage: `url(${x.bannerImg})` }}
                        >
                            <Container>
                                <Row>
                                    <Col md="10" className="offset-md-1">
                                        <div className="center-text">
                                            <div className="text-center">
                                                <div className="header-text bold-text">
                                                    <h1>{x.title}</h1>
                                                </div>
                                                <div className="header-sub-text">
                                                    <p className="text-white p-light">
                                                        {x.detail}
                                                    </p>
                                                </div>
                                                <div className="link-horizontal">
                                                    <ul className="justify-content-center">
                                                        <li>
                                                            <a className=" btn btn-default">
                                                                {x.btn1}
                                                            </a>
                                                        </li>
                                                        <li>
                                                            <a className=" btn btn-default">
                                                                {x.btn2}
                                                            </a>
                                                        </li>
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                    </Col>
                                </Row>
                            </Container>
                        </div>
                    </div>
                )
                )
            }
        </Slider>
        <Container>
            <Row>
                {CounterData.map((item, i) => {
                    return (
                        <Col xl="3" xs="6" className="counter-container" key={i}>
                            <div className="counters">
                                <img alt="" className="img-fluid counter-img" src={item.img} />
                                <div className="counter-text">
                                    <h3 className="count-text counts">
                                        <CountUp end={item.count} /></h3>
                                    <h5 className="count-desc">{item.desc}</h5>
                                </div>
                            </div>
                        </Col>
                    )
                })}
            </Row>
        </Container>
    </section>
)

export default Counter;